import logo from './logo.svg';
import './App.css';
import { useState } from 'react';
import {evaluate} from 'decimal-eval';

function App() {
  const [count, setCount] = useState(0);
  const [prevCount, setPrevCount] = useState(0);
  const [operation, setOperation] = useState(false);
  const [decimal, setDecimal] = useState(false);


  function allClear(){
    setCount(0);
    setPrevCount(0);
    setOperation(false);
    setDecimal(false);
  }

  function enterNum(num){
    if (operation===false) {
      if(decimal===false){ 
      const result=prevCount*10+num;
      setCount(result);
      setPrevCount(result); 
    } else {
      setCount(count+num);
      setPrevCount(prevCount+num);  
      }      
    } else { 
      if(decimal===false){
      const result=count*10+num;
      setCount(result);
      setPrevCount(prevCount+num); 
    } else {
      const result=count*10+num;
      setCount(count+num);
      setPrevCount(prevCount+num);     
    }
    }
  }

  function addNum(){
    if(prevCount!==0) {
    setPrevCount(prevCount.toString()+'+');
    } else {
      setPrevCount(count.toString()+'+');
    }
    setCount(0);
    setOperation(true);
    setDecimal(false);
  }

  function subtractNum(){
    if(prevCount!==0) {
       setPrevCount(prevCount.toString()+'-');
    } else {
      setPrevCount(count.toString()+'-');
    }
    setCount(0);
    setOperation(true);
    setDecimal(false);
  }

  function divideNum(){
    if(prevCount!==0) {
    setPrevCount(prevCount.toString()+'/');
    } else {
      setPrevCount(count.toString()+'/');
    }
    setCount(0);
    setOperation(true);
    setDecimal(false);
  }

  function multiplyNum(){
    if(prevCount!==0) {
    setPrevCount(prevCount.toString()+'*');
    } else {
      setPrevCount(count.toString()+'*');
    } 
    setCount(0);
    setOperation(true);
    setDecimal(false);
  }

  function getResult(){
    const str=prevCount.toString().replace(/[\+\-\*\/]+(?=[\+\*\/]|[\-]{2})/g,'');
    const finalResult = evaluate(str);
    setCount(finalResult);
    setPrevCount(0);
    setDecimal(false);
    setOperation(false);
    }

  function getDecimal() {
    if(decimal===false){
      setDecimal(true);
      const strCount=count.toString();
      const strPrevCount=prevCount.toString();
      setCount(0);
      setCount(strCount+'.');
      setPrevCount(strPrevCount+'.')
    }  
    }
  

  return (
    <div className="App">
      <div class="calculator">
        <div class="formulaScreen">{prevCount}</div>
        <div class="outputScreen" id="display">{count}</div>
        <div class="buttons">
          <button class="jumbo" id="clear" value="AC" onClick={allClear}>AC</button>
          <button id="divide" value="/" onClick={divideNum}>/</button>
          <button id="multiply" value="x"onClick={multiplyNum}>x</button>
          <button id="seven" value="7" onClick={()=>enterNum(7)}>7</button>
          <button id="eight" value="8" onClick={()=>enterNum(8)}>8</button>
          <button id="nine" value="9" onClick={()=>enterNum(9)}>9</button>
          <button id="subtract" value="-"onClick={subtractNum}>-</button>
          <button id="four" value="4" onClick={()=>enterNum(4)}>4</button>
          <button id="five" value="5" onClick={()=>enterNum(5)}>5</button>
          <button id="six" value="6" onClick={()=>enterNum(6)}>6</button>
          <button id="add" value="+" onClick={addNum}>+</button>
          <button id="one" value="1" onClick={()=>enterNum(1)}>1</button>
          <button id="two" value="2" onClick={()=>enterNum(2)}>2</button>
          <button id="three" value="3" onClick={()=>enterNum(3)}>3</button>
          <button class="jumbo" id="zero" value="0" onClick={()=>enterNum(0)}>0</button>
          <button id="decimal" value="." onClick={getDecimal}>.</button>
          <button id="equals" value="=" onClick={getResult}>=</button>
        </div>
      </div>
    </div>
  );
}

export default App;
